.features {
  min-height: 562px;
  padding-top: 112px;
  padding-bottom: 79px;
  padding-right: 0;

  background-color: #e3ecf6;
  background-image: url("../img/landscape-2-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/landscape-2-desktop@2x.jpg");
  }
}

@media screen and (max-width: 1365px) {
  .features {
    background-image: url("../img/landscape-2-tablet@1x.jpg");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/landscape-2-tablet@2x.jpg");
    }
  }
}

@media screen and (max-width: 767px) {
  .features {
    background-image: url("../img/landscape-2-mobile@1x.jpg");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/landscape-2-mobile@2x.jpg");
    }
  }
}

@media screen and (min-width: 1366px) {
  .features {
    padding-right: 0;
    align-items: center;
  }
}

@media screen and (max-width: 1365px) {
  .features {
    padding-right: 0;
  }
}

@media screen and (max-width: 767px) {
  .features {
    padding-right: 15px;
  }
}

@media screen and (max-width: 767px) {
  .features {
    padding-top: 58px;
    padding-bottom: 36px;
    padding-left: 15px;
  }
}

