.catalog {
  background-color: #f9fbfd;
  padding-bottom: 120px;
}

.catalog__title {
  padding-top: 120px;

  @media (max-width: 1365px) {
    padding-top: 0px;
  }

  @media (max-width: 767px) {
    padding-top: 0px;
  }
}

@media screen and (max-width: 767px) {
  .catalog {
    padding-top: 59px;
    padding-bottom: 35px;
    padding-left: 25px;
    padding-right: 25px;
  }
}

.catalog__wrapper-heading {
  display: flex;
  justify-content: center;
  padding-bottom: 31px;
}

@media screen and (max-width: 1365px) {
  .catalog__wrapper-heading {
    justify-content: flex-start;
    padding-bottom: 49px;
    padding-left: 0;
  }
}

@media screen and (max-width: 767px) {
  .catalog__wrapper-heading {
    padding-left: 183px;
  }
}

@media screen and (max-width: 767px) {
  .catalog__wrapper-heading {
    padding-left: 0;
    padding-bottom: 28px;
  }
}

.catalog h2 {
  margin: 0 auto;

  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: #011c40;
}

@media screen and (max-width: 1365px) {
  .catalog h2 {
    margin: 0 auto;

    font-size: 26px;
    line-height: 39px;
  }
}

@media screen and (max-width: 767px) {
  .catalog h2 {
    margin: 0 auto;
  }
}

@media screen and (max-width: 767px) {
  .catalog h2 {
    margin: 0 auto;
    font-size: 24px;
    line-height: 36px;
  }
}

@media screen and (max-width: 767px) {
  .catalog h2 {
    margin: 0 auto;
  }
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;

  @media (max-width: 1365px) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }

  @media (max-width: 767px) {
    justify-content: center;
    align-items: center;
    grid-template-columns: 0.5fr;
    grid-template-rows: repeat(8, 1fr);
    grid-column-gap: 30px;
  }
}

@media screen and (max-width: 1365px) {
  .catalog__list {
    min-width: 100%;
  }
}

@media screen and (max-width: 1365px) {
  .catalog__list {
    justify-content: space-around;
  }
}

@media screen and (max-width: 767px) {
  .catalog__list {
    justify-content: flex-start;
  }
}

@media screen and (max-width: 767px) {
  .catalog__list {
    justify-content: space-around;
  }
}

@media screen and (max-width: 767px) {
  .catalog__list {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 767px) {
  .catalog__list {
    flex-direction: flex-start;
    align-items: flex-start;
    padding: 0;
  }
}

.cards__item {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 1365px) {
  .cards__item {
    margin: 0;
    margin-right: 0;
  }
}

@media screen and (max-width: 767px) {
  .cards__item {
    margin-right: 0;
  }
}
